<template>
  <div class="lock">
    <HeadTop :isHome="isHome" :lang="lang" @changeAppLocale="changeAppLocale" />


 <div class="change">
      <div class="title">
       添加流动性
      </div>
      <div class="coin">
        <div class="coin_top">
          <!-- <div class="coin_img"> <img src="../../assets/img/coin.png" alt="" width="35" /></div> -->
          <img src="../../assets/img/ido/m.png" alt="" width="25" />
          <span class="m">MXT</span>
          
        </div>
        <p>余额：4515.244</p>
        <div class="put-box">
           <input type="number" v-model="money" placeholder="0" @input="inputEnter" />
           <button>MAX</button>
        </div>
       
      </div>
      <img src="../../assets/img/ido/change.png" alt="" class="change_icon" />
      <div class="coin">
        <div class="coin_top">
          <!-- <div class="coin_img"> <img src="../../assets/img/coin.png" alt="" width="35" /></div> -->
          <img src="../../assets/img/ido/usdt.png" alt="" width="25" />
          <span class="u">USDT</span>
           
        </div>
        <p>余额：4515.244</p>
       <div class="put-box">
           <input type="number" v-model="money" placeholder="0" @input="inputEnter" />
           <button style="background:#26A17B">MAX</button>
        </div>
        <!-- <input type="number" disabled v-model="1"/> -->
      </div>
       <div class="info " style="margin-bottom:0">
        <van-row>
          <van-col span="24">
            <div style="margin:20px 0 0px">
              <span class="">价格和流动性池份额</span>
            </div>
          </van-col>
        </van-row>
        <van-row>
          <van-col span="12">
            <div class="box">
              <span>USDT/MXT</span>
              <h4>{{dogesInfo?dogesInfo.info[10]:0 | figureFive}}</h4>
            </div>
          </van-col>
          <van-col span="12">
            <div class="box">
              <span>MXT/USDT</span>
              <h4 >{{dogesInfo?dogesInfo.info[10]:0 | figureFive}}</h4>
            </div>
          </van-col>
        </van-row>
        <van-row>
          <van-col span="24">
            <div class="box">
              <span class="">流动性池中的份额</span>
               <h4>{{dogesInfo?dogesInfo.info[10]:0 | figureFive}}</h4>
            </div>
          </van-col>
        </van-row>
      </div>
      <!-- <button class="onBtn">{{ $t("message.f5_7") }}</button> -->
      <button class="onBtn" v-if="dogesInfo&&getTimeFun(dogesInfo.info[4])>0">{{ $t("message.f5_30") }}</button>
      <button class="Btn" v-else @click="investIn">确 定</button>
    </div>
    <div class="jies"><p>通过添加流动性，您将赚取该币对所有交易额的0.17%，与您在流动性池中的份额成正比。赚取的费用会添加到流动性池中，实时累积，可以通过解除流动性来领取。</p>  </div>

    <div class="yu">
      <van-row>
          <van-col span="18" class="fixdBox">
            <div class="">
              <img src="../../assets/img/ido/m.png" alt="" width="25" /> 
              <img src="../../assets/img/ido/usdt.png" alt="" width="25" style="margin-left:-10px"/> 
            </div>
            <div class="">
              <p>USDT/MXT</p>
              <span>0.0004413</span>
            </div>
          </van-col>
          <van-col span="6">
            <div class="textRight">
             <img src="../../assets/img/ido/v.png" alt="" width="15">
             <!-- <img src="../../assets/img/ido/v1.png" alt=""> -->
            </div>
          </van-col>
        </van-row>
        <div class="info">
          <ul>
            <li class="u"><img src="../../assets/img/ido/usdt.png" alt="" width="18" /> 已入池USDT<span>84791.314</span></li>
            <li  class="m"><img src="../../assets/img/ido/m.png" alt="" width="18" /> 已入池MXT<span>84791.314</span></li>
            <li  class="m">流动性池中的份额<span>100.00%</span></li>
          </ul>
        </div>
          <button class="onBtn" v-if="dogesInfo&&getTimeFun(dogesInfo.info[4])>0">移 除</button>
      <button class="Btn" v-else @click="investIn">移 除</button>
    </div>
    <FontCon />
  </div>
</template>

<script type="es6">
import { mapActions, mapState } from "vuex";
import FontCon from "../../components/Footer";
import HeadTop from "../../components/Header";
import Web3 from "web3";
export default {
  data() {
    return {
      time: 43984932483,
      money:"",
      input_cols:false,
      timer:null,
      timer2:null,
      // progressWidth: 30,
    };
  },
    components: {
    HeadTop,
    FontCon,
  },
  computed: {
    ...mapState("main", [
      "dogesInfo",
      "Balance",
      "FIVEBalance",
      "account",
      // "balance",
      "poolContract",
      // "mxtContract",
      // "hmdBalance",
    ]),
  },
  methods: {
    ...mapActions("main", [
      // "add_price",
      "add_DOGESpool",
      "add_dogCoinContract",
      // "add_hmdCoinContract",
    ]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      localStorage.setItem("lang", lang);
      // this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
     goTo(){
 //直接跳转
 this.$router.push('/');
 },
    getFiveNum(num){
       var _this =this;
      if (_this.dogesInfo == null) {
        _this.$toast(_this.$t("message.noMask"));
        return;
      }
      if(num){
        console.log()
       return (num*10**18) / this.dogesInfo.info[7]*this.dogesInfo.info[6];
        
      }else{
        return 0;
      }
    },
    getFIVE(num1,num2){
      var num = web3.utils.fromWei(num1, 'ether');
       var a = web3.utils.fromWei(num2, 'Gwei');
      return Number(num*a)
     
    },
    inputEnter(){
      var _this =this;
      if (_this.dogesInfo == null) {
        _this.$toast(_this.$t("message.noMask"));
        return;
      }
      var c = _this.money .toString();
      var a =c.split('.')[1];
      var b = (web3.utils.fromWei(_this.dogesInfo.info[7], 'ether')).split('.')[1];
      if(a&&a.length>1){
        // this.input_cols=true;
        // if(!/^\d+(\.\d{1,6})?$/.test(_this.money))
        var len = c.split('.')[0].length+1
        _this.money=Number(c.substring(0,len+1));
        console.log(len)
         _this.$toast(this.$t("message.f5_20"));
           return
      }
      
    },
    getTimeFun(time){
      if(time>0){
   var nowtime = new Date();
    var lefttime = time*1000 - nowtime.getTime();//距离结束时间的毫秒数
    return lefttime;
      }else{
         return 0;
      }
    
    },
       withdraw: function () {
      if (this.dogesInfo == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
    //  if(this.dogesInfo.info[10]=0){
    //    this.$toast(_this.$t("message.f5_21"));
    //    return
    //  }
      this.$toast.loading({
        message: 'loading',
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.poolContract.methods
        .withdraw()
        .send({ from: account })
        .then((res) => {
          console.log("withdraw", res);
            _this.$toast.clear();
         _this.$toast.success(_this.$t("message.f5_16"));
            _this.add_DOGESpool();
            _this.add_dogCoinContract();
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    investIn(){
      var _this=this;
      var account =this.account;
      if (this.dogesInfo == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      if(!this.money){
        this.$toast(_this.$t("message.f5_22"));
        return;
      }
      if(this.money>(0.5-Web3.utils.fromWei(_this.dogesInfo.info[8], 'ether'))){
        this.$toast(_this.$t("message.f5_23"));
        return;
      }
      console.log((_this.money*10**18) / this.dogesInfo.info[7],'2222222222222222222')
         this.$toast.loading({
        message: 'loading',
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(_this.money*10**18)
      // return
          this.poolContract.methods
        .investIn()
        .send({ from: account ,value:_this.money*10**18})
        .then((res) => {
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.f5_16"));
            _this.add_DOGESpool();
            _this.add_dogCoinContract();
          // _this.add_lpCoinContract();

          _this.money = 0;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });

    }
  },
  mounted() {
    var lang = localStorage.getItem("lang");
    this.changeLanguage(lang || "en");


    var getData = () => {
        this.add_DOGESpool();
    this.add_dogCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.dogesInfo || !this.Balance || !this.FIVEBalance || !this.poolContract) {
          getData();
        } else {
          // this.getCoinBalance();
          // this.isApproveFun();
          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 30000);
  },
    destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.line {
  height: 7px;
  background: #232323;
  width: 100%;
}
.lock {
   background: url(../../assets/img/ido/top.png) top center no-repeat #25282C;
  background-size: 100%;
  color: #fff;
  padding: 400px 0 0;
  .jies{
    p{
         background: url(../../assets/img/ido/text.png) top center no-repeat;
  background-size: 100%;
    }
    width: 92%;
    margin: 30px auto;
    padding:10px 20px;
    background:linear-gradient(to right,#2669ea,#b356fd);
    border-radius: 10px;
    line-height: 24px;
  // background-size: 100%;
  color: #fff;
  box-sizing: border-box;
  }
  .change {
    width: 92%;
    margin: 30px auto;
    box-sizing: border-box;
    background: #34383F;
    border-radius: 10px;
    padding: 20px 20px;
    position: relative;
    .title {
      font-size: 20px;
      font-family: Microsoft YaHei;
      font-weight: bold;
      color: #ffffff;
      p {
        float: right;
        font-weight: normal;
        font-size: 14px;
        span {
          color: #ff8e21;
        }
      }
    }
    .title::after {
      content: "";
      display: block;
      width: 80px;
      height: 11px;
       background: url(../../assets/img/ido/title.png) repeat top;
    background-size: 100%;
      position: absolute;
      top: 0;
      // bottom: -10px;
    }
    .coin{
      position: relative;
      p{
        position: absolute;
        right: 0;
        top: 10px;
      }
    }
    .coin_top {
      margin: 20px 0 10px;
      display: flex;
      align-items: center;
      img {
        vertical-align: middle;
        margin-right: 6px;
      }
      span {
     font-size:20px;
    font-family: Impact;
    font-weight: 400;
    color: #9574FF;
      }
       span.u{
         color: #26A17B;
       }
    }
    .put-box{
       background: #fff;
      border-radius: 10px;
      position: relative;
         input,.input_l {
      background: #fff;
      border-radius: 10px;
      border: none;
      width: 100%;
      font-size: 24px;
      padding: 12px 12px;
      box-sizing: border-box;
      color: #34383F;
      font-family: Bebas;
    }
    button{
      position: absolute;
      top: 0;
      right: 0;
      border: none;
      height: 55px;
      line-height: 55px;
      padding: 0 8px;
      font-size: 16px;
      color: #1D2A42;
      font-weight: 600;
      background: #9574FF;
      border-radius:0 10px 10px 0;
    }
    }
 
    .change_icon {
      width: 25px;
      margin: 20px auto 0;
      display: block;
    }

    .five_img {
      width: 100px;
      display: block;
      margin: 0 auto 5px;
    }
    .info {
      margin: 30px 0;
      .box{
        margin: 15px 0;
        span{
          color: #9574FF;
          font-size: 13px;
        }
      }
      h4 {
        font-size: 15px;
        // font-family: Impact;
        // font-weight: 400;
        color: #fff;
        padding: 6px 2px 0;
      }
    }
  
  } 
      .onBtn {
      display: block;
       width:85%;
      margin: 30px auto 20px;
      background: linear-gradient(0deg, #85a4a2 0%, #a2b2b4 100%);
      border-radius: 10px;
      padding: 15px 20px;
      font-size: 18px;
      font-family: Microsoft YaHei;
      font-weight: bold;
      color: #4e605f;
      .van-count-down{
        line-height: 26px;
        color: #4e605f;
      }
    }
  .Btn {
      display: block;
      width:85%;
      margin: 30px auto 30px;
      background: linear-gradient(0deg, #8360e9  0%, #9574FF 100%);
      border-radius: 10px;
      padding: 15px 20px;
      font-size: 18px;
      font-family: Microsoft YaHei;
      font-weight: bold;
      border: none;
      color: #25282C;
    }
  .yu{
        width: 92%;
    margin: 30px auto;
    box-sizing: border-box;
    background: #34383F;
    border-radius: 10px;
    padding: 20px 20px;
    position: relative;
    .fixdBox{
      display: flex;
    align-items: start;
    div{
      margin-right: 10px;
      p{
             font-size: 24px;
        font-family: Impact;
        font-weight: 400;
        color: #9574FF;
        line-height: 22px;
        margin-bottom: 8px;
      }
    }
   
    }
  } 
  ul{
        background: #23262F;
        border-radius: 10px;
        padding:20px 10px;
        margin-top: 20px;
        li{padding: 4px 0;}
        img{
          vertical-align: middle;
        }
        span{float: right;}
    }
    .u{color: #26A17B;}
    .m{color: #9574FF;}
}
.tips{
  margin: 10px 0;
    color: #a9a8a6;
}
@media only screen and (max-width: 391px) {
  .van-count-down {
    font-size: 24px;
  }
  .home .time_box .time span {
    margin-bottom: 12px;
  }
  .info span {
    font-size: 12px;
  }
  .home .change .info h3,
  .home .change .info h2 {
    font-size: 20px;
    padding: 2px 0 0;
  }
  .home .top .turnover {
    margin-top: 18px;
  }
}
</style>



