<template>
  <div class="home">
    <HeadTop :isHome="isHome" :lang="lang" @changeAppLocale="changeAppLocale" />
    <div class="top">
      <img src="../../assets/img/defi_1.png" alt="" width="90%"/>
      <h2>{{ $t("message.f5_47") }}</h2>
      <h1>0.00 <small>CAKE</small></h1>
      <div class="time">
        
        <van-count-down millisecond :time="time" format="HH:mm:ss:SS" /></div>
      <p>{{ $t("message.f5_48") }}</p>
      <div class="num">--</div>
       <p>{{ $t("message.f5_49") }}</p>
      <div class="num">0.00 CAKE</div>
    </div>

     <div class="top color_bg">
      <img src="../../assets/img/defi_2.png" alt="" width="100%"/>
      <p>{{ $t("message.f5_50") }}</p>
      <div class="num">0.00</div>
       <p>{{ $t("message.f5_51") }}</p>
      <div class="num">0.00</div>
    </div>

      <div class="cake">
          <div class="cakeBox">
            <img src="../../assets/img/cake_logo.png" alt="" width="40%">
              <p>{{ $t("message.f5_50") }}</p>
              <div class="num">0.00</div>
              <p>{{ $t("message.f5_51") }}</p>
              <div class="num">0.00</div>
              <div class="btnbox">
          <button class="in">{{ $t("message.f5_52") }}</button>
        </div>
          </div>
      </div>
      <div class="color_bg">
        <div class="bot_top">
          <img src="../../assets/img/cake_logo.png" alt="" width="40">
          <h1>{{ $t("message.f5_53") }}</h1>
          <h3> <img src="../../assets/img/token.png" alt="" width="40">MXT {{ $t("message.f5_14") }}0.00</h3>
          <div><input type="number" placeholder="0"></div>
          <button>{{ $t("message.f5_16_1") }}</button>
        </div>
        <ul>
          <li>{{ $t("message.f5_19") }}<span>0.00MXT</span></li>
          <li>{{ $t("message.f5_20") }}<span>0.00MXT</span></li>
          <li>{{ $t("message.f5_54") }}<span>--</span></li>
        </ul>
         <div class="btnbox">
          <button class="in">{{ $t("message.f5_18") }}</button>
        </div>
      </div>
    <FontCon />
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import FontCon from "../../components/Footer";
import { mapActions, mapState } from "vuex";
import Web3 from "web3";
export default {
  data() {
    return {
      lang: "",
      currency: "",
      link: "--",
      coinBalance: 0,
      isApprove: false,
      stakeAmount: null,
      withdrawAmount: "",
      timer: null,
      timer2: null,
      show: false,
      isHome: true,
      nbn: null,
      time: "0",
    };
  },
  computed: {
    ...mapState("main", [
      // "priceHMD",
      // "priceCNTM",
      "hmdInfo",
      "account",
      "balance",
      "mxtCoinContract",
      "mxtContract",
      "hmdBalance",
    ]),
  },
  components: {
    HeadTop,
    FontCon,
  },
  methods: {
    ...mapActions("main", [
      // "add_price",
      "add_hmdInfo",
      "add_mxtCoinContract",
      "add_hmdCoinContract",
    ]),
    showPop: function (arg, callback) {
      if (arg == "show") {
        this.show = true;
      }
    },
    pirceInput(type) {
      if (type == 1) {
        let str = "" + this.stakeAmount;
        if (str.indexOf(".") !== -1) {
          let arr = str.split("");
          arr.splice(arr.length - 1);
          let str2 = arr.join("");
          this.stakeAmount = +str2;
        }
      } else {
        let str = "" + this.withdrawAmount;
        if (str.indexOf(".") !== -1) {
          let arr = str.split("");
          arr.splice(arr.length - 1);
          let str2 = arr.join("");
          this.withdrawAmount = +str2;
        }
      }
    },
    protectAddress: function (addr, num) {
      if (addr) {
        var newAddr = addr.replace('"', "").replace('"', "");
        if (newAddr == "0x0000000000000000000000000000000000000000") {
          return "--";
        }
        if (!newAddr) {
          return "--";
        }
        if (newAddr == 1) {
          return newAddr;
        }
        return (newAddr + "").length == 42
          ? newAddr.substring(0, num) + "****" + newAddr.substring(39, 42)
          : newAddr;
      }
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    ceilNum(d, prex = 4) {
      if (parseInt(d) == d) return d;
      let len = 1;
      let i = 0;
      while (parseInt(d) != d) {
        d = d * 10;
        len *= 10;
        i++;
      }
      let last = Math.abs(i <= prex ? 0 : d % Math.pow(10, i - prex));
      let d1 = d - last;
      if (last > 0) {
        d1 = d1 + Math.pow(10, i - prex);
      }
      d1 = d1 / len;
      return d1;
    },
    getNbn(e, type) {
      if (type == 1) {
        var val = e;
      } else {
        var val = e.target.value;
      }
      if (!e) return;

      this.mxtContract.methods
        .getTokenPrice(
          "0xEc0029a08BcD725Ff3327288bEFB99ecEE29Bc36",
          "0x6e42eae3317a19eb4b5115b0bce388bbf21cb917",
          "1000000000"
        )
        .call()
        .then((res) => {
          var five = this.ceilNum(Web3.utils.fromWei(res, "ether"));
          this.nbn = five * this.stakeAmount;
          // console.log(res,Web3.utils.fromWei(res, 'ether'),Web3.utils.toWei(val, 'ether'))
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    //取消抵押
    withdraw: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      var myzy = web3.utils.fromWei(_this.hmdInfo.info[19], "ether");
      if (myzy <= 0) {
        _this.$toast(_this.$t("message.noZy"));
        return;
      }
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .exit()
        .send({ from: account })
        .then((res) => {
          console.log("withdraw", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_hmdInfo();
          _this.getCoinBalance();
          _this.add_hmdCoinContract();
          // }

          _this.withdrawAmount = "";
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    max: function () {
      var num = this.coinBalance;
      num = num.toString();
      let index = num.indexOf(".");
      if (index !== -1) {
        num = num.substring(0, 6 + index + 1);
      } else {
        num = num.substring(0);
      }

      this.stakeAmount = num;
      this.getNbn(num, 1);
    },
    stake: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var inviteCode = _this.hmdInfo.inviteCode.toString();
      var referrer = _this.hmdInfo.referrer.toString();
      var account = _this.account;

      if (!_this.isApprove) {
        _this.$toast(_this.$t("message.noAccredit"));
        return;
      }

      if (!referrer || referrer == "--") {
        console.log(11111, referrer);
        referrer = _this.$route.query.invite
          ? _this.$route.query.invite.toString()
          : 1;
        console.log(222222, referrer);
      }
      if (!inviteCode || inviteCode == "--") {
        inviteCode = _this.account;
      }

      if (inviteCode == referrer) {
        referrer = "1";
      }

      if (Number(_this.stakeAmount) > Number(_this.coinBalance)) {
        _this.$toast(_this.$t("message.noBanner"));
        return;
      }
      if (Number(_this.balance / 10 ** 18) < 0) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      if (Number(_this.stakeAmount) <= 0) {
        _this.$toast(_this.$t("message.noNum"));
        return;
      }
      if (Number(_this.stakeAmount) < 100) {
        _this.$toast(_this.$t("message.f_67"));
        return;
      }
      if (!referrer || referrer == "--") {
        _this.$toast(_this.$t("message.onReferees"));
        return;
      }
      console.log(
        inviteCode,
        referrer,
        _this.stakeAmount * 10 ** 18,
        web3.utils.toWei(_this.stakeAmount, "ether")
      );
      var monye = (_this.stakeAmount * 10 ** 18).toString();
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });

      this.mxtContract.methods
        .investIn(
          inviteCode,
          referrer,
          web3.utils.toWei(_this.stakeAmount, "ether")
        )
        .send({
          from: account,
        })
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_hmdInfo();
          _this.getCoinBalance();
          _this.add_hmdCoinContract();
          //   setTimeout(function(){
          //      _this.add_hmdInfo();
          //     _this.getCoinBalance();
          //     _this.add_hmdCoinContract();
          //   },10000)
          // }

          _this.stakeAmount = 1;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);

      // this.$router.push({
      //   name: this.$route.name,
      //   params: { lang: this.lang },
      //   query: { r: localStorage.getItem("r") },
      // });
    },
    // 授权
    lpApprove: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.hmdInfo.address;
      var account = this.account;
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getUrl: function () {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + hash;
      var newAddr = "";
      if (this.hmdInfo) {
        if (this.hmdInfo.referrer) {
          var addr = this.hmdInfo.inviteCode;
          newAddr = addr.replace('"', "").replace('"', "");
          this.link = url + "?r=" + newAddr;
        } else {
          this.link = url;
        }
      }
      return this.link;
    },
    // 是否授权
    isApproveFun: function () {
      var account = this.account;
      var address = this.hmdInfo.address;

      this.mxtCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
    //获取当前币种余额
    getCoinBalance: function () {
      if (this.mxtCoinContract) {
        var account = this.account;
        this.mxtCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            this.coinBalance = num;
          });
      }
    },
    get_param: function (name) {
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      var r = window.location.search.substr(1).match(reg);
      if (r != null) {
        return unescape(r[2]);
      }
      // console.log(r)
      return null;
    },
  },
  created() {
    this.add_mxtCoinContract();
    this.getCoinBalance();
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    this.currency = "HMD";
    if (this.$route.query.r) {
      localStorage.setItem("r", this.$route.query.r);
    }

    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
      this.add_hmdCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtCoinContract || !this.hmdBalance) {
          getData();
        } else {
          this.getCoinBalance();
          this.isApproveFun();
          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.home{background: #23262F;}
.btnbox {
  text-align: center;
  button {
    background: transparent;
    border: 1px solid #fff;
    color: #fff;
    border-radius: 40px;
    padding: 14px 0;
    min-width:55%;
    text-align: center;
    margin: 0 10px;
  }
  .in {
    background: #3772ff;
    border: none;
  }
}
.color_bg{background: #1B1D23 !important; padding-top: 30px !important;}
.top {
  // margin: 0 0 0;
  text-align: center;
  padding-bottom: 40px;
  padding-top: 90px;
  h2{
    text-align: center;
    font-size: 20px;
    font-weight: normal;
    letter-spacing: 1px ;
  }
  h1 {
    font-size: 24px;
    margin: 12px 0;
    text-align: center;
  }
  .time{
    min-width: 60%;
    display: inline-block;
    margin: 0 auto;
    background: #141416;
    border-radius: 5px;
    padding: 20px;
    .van-count-down{text-align: center;font-size: 24px;}
  }
  p {
    font-size: 16px;
    margin: 30px 0 20px;
    letter-spacing: 1px;
  }
  .num{
    background: #353945;
    min-width:60% ;
    display: inline-block;
    border-radius: 30px;
    padding: 10px;
    color: #006EE5;
    font-size: 18px;
    font-weight: bold;
  }
}
.cake{
    background: url(../../assets/img/cake_bg.png) top center no-repeat;
  background-size: 100%;
  text-align: center;
    padding: 40px 0;
    .btnbox{margin-top: 40px;}
.cakeBox{
  width: 70%;
  margin: 0 auto;
  border: 5px solid #fff;
  border-radius: 30px;
  padding: 30px 0;
}
   p {
    font-size: 16px;
    margin: 30px 0 20px;
    letter-spacing: 1px;
  }
  .num{
    background: #353945;
    min-width:60% ;
    display: inline-block;
    border-radius: 30px;
    padding: 10px;
    color: #006EE5;
    font-size: 18px;
    font-weight: bold;
  }
}
.color_bg{
  text-align: center;
  padding: 30px 4%;
  .bot_top{
     background: url(../../assets/img/bot_top.png) top center no-repeat;
  background-size: 100%;
  border-radius: 10px;
  padding: 10px 0;
  h1{margin: 5px 0 0;font-size: 16px;letter-spacing: 1px;}
  h3{display: flex;    align-items: center;
    justify-content: center;font-weight: normal; 
    }
    input{
     border-radius: 5px;
     border: none;
     background: #fff;
     font-size: 16px;
     padding:7px 10px;
     color: #23262F;
     margin-top: 4px;
    }
    button{
      color:#006EE5 ;
      background: #fff;
      border-radius: 10px;
      border: none;
      padding: 8px 20px;
      min-width: 100px;
      margin-top: 15px;
    }
  }
  ul{
    text-align: left;
    margin: 40px 0;
    li{margin: 15px 10px;font-size: 15px;}
    span{float: right;color:#006EE5 ;font-size: 16px;}
  }
}
</style>



