<template>
  <div class="lock">
    <HeadTop :lang="lang" @changeAppLocale="changeAppLocale" />

    <div class="change">
      <div class="title">
       {{ $t("message.f5_1") }}
      </div>
       <div class="info " style="margin-bottom:0">
        <van-row>
          <van-col span="24">
            <div class="">
              <span class="">{{ $t("message.f5_2") }}</span>
              <h1>{{LockPoolInfo ? LockPoolInfo.info[6] : '0' | figureK}} <small>≈ {{LockPoolInfo ? (Number(LockPoolInfo.info[6]) * Number(LockPoolInfo.MXTPrice)) : '0' | figureK}} USDT</small> </h1>
            </div>
          </van-col>
        </van-row>
        <van-row>
          <van-col span="24">
            <div class="">
              <span class="">{{ $t("message.f5_3") }} MXT</span>
              <h1 style="color:#29f285">{{LockPoolInfo ? LockPoolInfo.info[4] : '0' | figureK }} <small>≈ {{LockPoolInfo ? (Number(LockPoolInfo.info[4]) * Number(LockPoolInfo.MXTPrice)) : '0' | figureK}} USDT</small> </h1>
            </div>
          </van-col>
        </van-row>
        <van-row style="margin-bottom:20px">
          <van-col span="12">
            <div class="">
              <span>{{ $t("message.f5_4") }}</span>
              <h4>{{LockPoolInfo ? LockPoolInfo.info[10] : '0'}}</h4>
            </div>
          </van-col>
           <van-col span="12">
            <div class="">
              <span>{{ $t("message.f5_3_1") }}（MXT）</span>
              <h4 >{{LockPoolInfo ? LockPoolInfo.info[8] : '0'  | figureK }}</h4>
            </div>
          </van-col>
        </van-row>
        <van-row style="margin-bottom:20px">
          <van-col span="12">
            <div class="">
              <span>{{ $t("message.f5_5") }}</span>
              <h4>{{LockPoolInfo ? LockPoolInfo.info[7] : '0'}}</h4>
            </div>
          </van-col>
          <van-col span="12">
            <div class="">
              <span>{{ $t("message.f5_6") }}(MXT)</span>
              <h4 >{{LockPoolInfo ? LockPoolInfo.info[9] : '0'  | figureK }}</h4>
            </div>
          </van-col>
        </van-row>
          <van-row>
        <van-col span="24">
            <div class="">
              <span>{{ $t("message.f5_7") }}</span>
              <h4 style="font-size:14px;font-family: auto;">{{ this.link }}</h4>
            </div>
          </van-col>
            </van-row>
      </div>
      <div class="tips">{{ $t("message.f5_8") }}</div>
       <van-row>
          <van-col span="12">
             <button class="Btn"
               v-clipboard:copy="link"
          v-clipboard:success="onCopy"
          v-clipboard:error="onError"
             >{{ $t("message.f5_9") }}</button>
          </van-col>
          <van-col span="12">
             <button class="Btn" v-if=" LockPoolInfo&&LockPoolInfo.info[6]>0" @click="getRewardFun">{{ $t("message.f5_10") }}</button>
             <button class="Btn"  v-else @click="investIn">{{ $t("message.f5_11") }}</button>
          </van-col>
        </van-row>
    </div>
 <div class="change">
      <div class="title">
       {{ $t("message.f5_12") }}
      </div>
      <div class="coin">
        <div class="coin_top">
          <span>{{ $t("message.f5_13") }}(USDT+MXT LP)</span>
        </div>
         <input type="number" v-model="money" placeholder="0" :disabled="input_cols" />
      </div>
      <div class="tips">USDT+MXT LP {{ $t("message.f5_14") }}{{coinBalance | figureK }}</div>
      <button class="Btn" v-if="isApprove" @click="investIn2">{{ $t("message.f5_15") }}</button>
      <button class="Btn" v-else @click="lpApprove">{{ $t("message.f5_16_1") }}</button>
       <!-- <button class="onBtn" v-else @click="lpApprove">{{ $t("message.f5_16_1") }}</button> -->
      <div style="    color: #E84F68;">
        {{ $t("message.f5_17") }}
      </div>

    </div>

     <div class="change">
      <div class="title">
       {{ $t("message.f5_18") }} LP
      </div>
<div class="info " style="margin-bottom:0">
       <van-row >
          <van-col span="12">
            <div class="">
              <span>{{ $t("message.f5_19") }}LP</span>
              <h2>{{LPliquidityInfo ? LPliquidityInfo.info[2] : '0' | figureK }} </h2>
              <div style="margin:5px 0 0">≈ {{LPInfo ? (Number(LPliquidityInfo.info[2]) /Number(LPInfo.totalSupply) * Number(LPInfo.liquidity) *2 ) : '0' | figureK}} USDT</div>
            </div>
          </van-col>
           <van-col span="12">
            <div class="">
              <span>{{ $t("message.f5_20") }} LP</span>
              <h3  >{{LPliquidityInfo ? LPliquidityInfo.info[3] : '0'  | figureK }}</h3>
              <div style="margin:5px 0 0">≈ {{LPInfo ? (Number(LPliquidityInfo.info[3]) /Number(LPInfo.totalSupply) * Number(LPInfo.liquidity) *2 ) : '0' | figureK}} USDT</div>
            </div>
          </van-col>
        </van-row>
         
</div>
 <div class="coin">
        <div class="coin_top">
          <span>{{ $t("message.f5_21") }}</span>
        </div>
         <input type="number" v-model="num" placeholder="0"  />
      </div>
      <button class="Btn" v-if="LPliquidityInfo && LPliquidityInfo.info[3]>0" @click="withdraw">{{ $t("message.f5_18") }}</button>
      <button class="onBtn" v-else >{{ $t("message.f5_18") }}</button>
    </div>


    <FontCon />
  </div>
</template>

<script type="es6">
import { mapActions, mapState } from "vuex";
import FontCon from "../../components/Footer";
import HeadTop from "../../components/Header";
import Web3 from "web3";
export default {
  data() {
    return {
      lang:"",
      time: 43984932483,
      money:"",
      input_cols:false,
      timer:null,
      timer2:null,
      link: '--',
      coinBalance:0,
      isApprove:false,
      num:0,

    };
  },
    components: {
    HeadTop,
    FontCon,
  },
  computed: {
    ...mapState("main", [
      "LockPoolInfo",
      "LockPool",
      "account",
      "Balance",
      "LPliquidity",
      "LPliquidityInfo",
      "LPCoin",
      "LPInfo"
    ]),
  },
  methods: {
    ...mapActions("main", [
      "add_LockPool",
      "add_LPliquidity",
      "add_LPCoinContract"
    ]),
     // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      localStorage.setItem("lang", lang);
      // this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },

       withdraw: function () {
      if (this.LockPool == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      var money = _this.num;
      console.log(money,Number(web3.utils.fromWei(_this.LPliquidityInfo.info[3])),Number(money)>Number(web3.utils.toWei(_this.LPliquidityInfo.info[3])))
      if(Number(money)>Number(web3.utils.fromWei(_this.LPliquidityInfo.info[3], 'ether')) ){
        this.$toast(this.$t("message.f5_22"));
        return;
      }
       if(Number(money)<=0){
        this.$toast(this.$t("message.f5_22"));
        return;
      }
      this.$toast.loading({
        message: 'loading',
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.LPliquidity.methods
        .withdraw(web3.utils.toWei(money.toString(), 'ether'))
        .send({ from: account })
        .then((res) => {
          console.log("withdraw", res);
            _this.$toast.clear();
         _this.$toast.success(_this.$t("message.f5_16"));
              this.add_LockPool();
        this.add_LPliquidity();
        this.add_LPCoinContract();
        _this.getCoinBalance();
        _this.num=0;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
      getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + "#/";
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
       getRewardFun: function () {
      if (this.LockPool == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.LockPool.methods
        .getReward()
        .send({ from: account })
        .then((res) => {
          console.log("getReward", res);
          // if (res.status) {
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_LockPool();
          _this.getCoinBalance();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    investIn(){
      var _this=this;
      var account =_this.account;
      var inviteCode = _this.LockPoolInfo.inviteCode;
      var referrer = _this.LockPoolInfo.referrer;
      if (_this.LockPool == null) {
        _this.$toast(_this.$t("message.noMask"));
        return;
      }
      if (!referrer || referrer == "--") {
        var invite = _this.$route.query.invite;
        referrer = invite.toString();
      }

      if (!inviteCode || inviteCode == "--") {
        inviteCode = _this.account;
      }

       if (Number(_this.Balance / 10 ** 18) < 0) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }

      console.log(inviteCode,referrer)
         this.$toast.loading({
        message: 'loading',
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      // return
          this.LockPool.methods
        .investIn(inviteCode,referrer)
        .send({ from: account,value:'50000000000000000' })
        .then((res) => {
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.f5_16"));
          this.add_LockPool();
        this.add_LPliquidity();
        this.add_LPCoinContract();
        _this.getCoinBalance();
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });

    },
     investIn2(){
      var _this=this;
      var account =_this.account;
      if (_this.LPliquidity == null) {
        _this.$toast(_this.$t("message.noMask"));
        return;
      }
     

       if (Number(_this.Balance / 10 ** 18) < 0) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
    var money = _this.money;
    
       if (Number(money) < 0) {
        _this.$toast(this.$t("message.f5_23"));
        return;
      }
        if (Number(money) > Number(_this.coinBalance / 10**18)) {
        _this.$toast('LP' + this.$t("message.f5_24"));
        return;
      }
         this.$toast.loading({
        message: 'loading',
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      // return
          this.LPliquidity.methods
        .investIn(web3.utils.toWei(money.toString(), 'ether'))
        .send({ from: account })
        .then((res) => {
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.f5_16"));
            _this.getCoinBalance();
                           this.add_LockPool();
        this.add_LPliquidity();
        this.add_LPCoinContract();
        this.money=0;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });

    },
      // 授权
    lpApprove: function () {
      if (this.LPCoin == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.LPliquidityInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.LPCoin.methods
        .approve(address, "5000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 是否授权
    isApproveFun: function () {
       if (this.LPCoin == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var account = this.account;
      var address = this.LPliquidityInfo.address;

      this.LPCoin.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log("usdt=========isApproveFun", account, address);
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
       //获取当前币种余额
    getCoinBalance: function () {
      if (this.LPCoin) {
        var account = this.account;
        this.LPCoin.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            this.coinBalance = res;
          });
      }
    },
  },
  mounted() {
    var lang = localStorage.getItem("lang");
    this.changeLanguage(lang || "en");
    var _this = this;

    var getData = () => {
        this.add_LockPool();
        this.add_LPliquidity();
        this.add_LPCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      _this.timer = setInterval(() => {
        if (!_this.LockPoolInfo || !_this.LPliquidity || !_this.LPCoin) {
          getData();
        } else {
          _this.getCoinBalance();
          _this.isApproveFun();
           if (_this.LockPoolInfo.inviteCode || _this.LockPoolInfo.referrer) {
            var invite = _this.LockPoolInfo.inviteCode || _this.LockPoolInfo.referrer;
            _this.getUrl(invite);
          }
          clearInterval(_this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 30000);
  },
    destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

