<template>
  <header>
    <div id="header">
      <van-row  type="flex" justify="space-between" gutter="10" align="center"> 
        <van-col span="12">
          <a  @click="hreftwo('/')">
            <img
              style="width: 160px;"
              src="../assets/img/logo.png"
            /> </a>
        </van-col>
        <van-col span="4" class="textRight">
          <img @click="showPopup" src="../assets/img/mune.png" alt="" width="25"
            />
        </van-col>
      </van-row>
      <!-- <van-row>
        <van-col span="18">
          <div class="bgWhite flex">
                <div class="bg_logo">
                  <img src="../assets/img/topLogo.png" alt="" />
                  <span>BSC</span>
                </div>
                <p>{{ protectAddress(this.account, 4) }}</p>
              </div>
        </van-col>
      </van-row> -->

      <van-popup
        v-model="show"
        position="left"
        :style="{ height: '100%', width: '65%' }"
      >
        <div class="menu">
          <img src="../assets/img/logo.png" alt="" width="55%" />
          <div class="list">
            <div class="bgWhite flex">
                <div class="bg_logo">
                  <img src="../assets/img/topLogo.png" alt="" />
                  <span>BSC</span>
                </div>
                <p>{{ protectAddress(this.account, 4) }}</p>
              </div>
                <div class="li_col">
              <a href="/">{{ $t("message.f5_55") }}</a>
            </div>
            <!-- <div class="li_col">
              <a @click="hreftwo('/liquid')">添加流动性</a>
            </div> -->
             <div class="li_col">
              <a @click="hreftwo('/destruction')">{{ $t("message.f5_56") }}</a>
            </div>
            <!-- <div class="li_col">
              <a @click="hreftwo('/home')">超级矿机</a>
            </div> -->
            <div class="li_col">
              <a @click="hreftwo('/defi')">{{ $t("message.f5_57") }}</a>
            </div>
           
            <div class="li_col">
              <a @click="tips">{{ $t("message.f5_58") }}</a>
            </div>

            <div class="li_col">
              <a href="https://bscscan.com/">{{ $t("message.f5_59") }}</a>
            </div>
            <div class="li_col">
              <a @click="tips">{{ $t("message.f5_60") }}</a>
            </div>
             <!-- <div class="li_col">
              <a @click="tips">审计报告</a>
            </div> -->
           
           <div class="li_col">
              <a>{{ $t("message.f5_61") }}</a>
              <ul>
                <li :class="lang == 'zhCHS'?'in':''" @click="changeLocale('zhCHS')">{{ $t("message.f5_62") }}<img v-if="lang == 'zhCHS'" class="gou" src="../assets/img/gou.png"/></li>
                 <li :class="lang == 'en'?'in':''" @click="changeLocale('en')" >{{ $t("message.f5_63") }} <img v-if="lang == 'en'" class="gou" src="../assets/img/gou.png"/></li>
              </ul>
            </div>
            <div class="li_col">
              <a href="https://metalabdapp.github.io/MetaLab.pdf">{{ $t("message.f5_64") }}</a>
            </div>
            <div class="li_col">
              <a href="https://metaslab.finance">{{ $t("message.f5_65") }}</a>
            </div>
       
          </div>
          <!-- <div class="foot">
            <a href="https://twitter.com/MetaLab_one"
              ></a>
            <a href="https://t.me/metalab_dao"
              ></a>
            <a href="https://ss.transgot.cn/metalab_dao"
              ></a>
          </div> -->
        </div>
      </van-popup>
    </div>
  </header>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      isShowNav: false,
      showRule: false,
      show: false,
      lang_1:null,
      langList: [
        {
          text: "中文",
          img_txt: "zhCHS",
        },
        {
          text: "EN",
          img_txt: "en",
        },
      ],
    };
  },
  props: {
    lang: {
      type: String,
      default: "zhCHS",
    },
    type: {
      type: String,
      default: "home",
    },
    isHome: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ...mapState("main", ["account", "oktInfo"]),
  },
  methods: {
    ...mapActions("main", ["add_oktInfo"]),
    showPopup() {
      this.show = true;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
    changeLocale(locale) {
      localStorage.setItem("lang", locale);
      this.$emit("changeAppLocale", locale);
      // this.isShowNav = !this.isShowNav;
    },
    LanguageClick: function () {
      this.isShowNav = !this.isShowNav;
    },
    tips() {
      this.$toast.fail(this.$t("message.tips3"));
      // this.$dialog.alert({
      //   message: this.$t("message.tips3"),
      //   theme: "round-button",
      // });
    },
    prev() {
      this.$router.go(-1);
    },

    // 	showPopup() {
    //   this.showRule = true;
    // },
    hreftwo(url) {
      // console.log(url.length);
      // if (url.length > 1) {
      //   this.$router.push({ path: url + "/" + this.lang });
      // } else {
      this.$router.push({ path: url });
      // }
    },
  },
};
</script>

<style scoped lang="less">
#header {
  padding: 15px 3% 10px;
  position: absolute;
  top: 0;
  width: 100%;
  box-sizing: border-box;
}
.bgWhite {
  padding: 5px 10px;
  background: #161414;
  border-radius: 6px;
  opacity: 0.8;
  margin-top: 10px;
  p {
    margin-left: 10px;
    height: 25px;
    line-height: 25px;
  }
}
.van-popup{background:rgba(35, 38, 47, 0.9)!important;}
.flex {
  display: flex;
}
.bg_logo {
  height: 25px;
  line-height: 25px;
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 600;
  color: #fff;
  // background: #f4f7fe;
  border-radius: 4px;
  // border: 1px solid #e1e1e1;
  padding: 0 4px;
  img {
    width: 20px;
    vertical-align: sub;
  }
}
.menu {
  padding: 30px 20px;
  position: relative;
  height: 90%;
  .list {
    border-top: 1px solid #08090a;
    margin-top: 20px;
    padding-top: 10px;
    img {
      vertical-align: bottom;
      float: left;
     margin-top: 2px;
    margin-right: 6px;
    }
    a {
      font-size: 16px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 500;
      color: #fff;
      display: block;
      padding: 12px 20px 12px 30px
    }
  }
   .li_col li.in{color:#006EE5;font-weight: bold;font-size: 15px;}
  .li_col li{
    padding: 5px 48px;
    color: #fff;
    font-size: 15px;
    .gou{
          width: 20px;
    float: none;
    margin-top: 0;
    margin-left: 20px;
    }
  }
  .foot {
    position: absolute;
    bottom: 20px;
    img {
      display: inline-block;
      margin-right: 8px;
    }
  }
}
</style>



