import { setStorage, getStorage } from '@/assets/js/units.js';
import getWeb3 from '@/assets/js/getWeb3.js'
import DOGES from '@/assets/coin_abi/DOGES.json'
import coin_lp from '@/assets/coin_abi/LP.json'
import LP from '@/assets/pool_abi/pool_LP.json'
import DOGE_pool from '@/assets/pool_abi/pool_DOGE.json'
import depositPool from '@/assets/pool_abi/deposit_pool.json'
const state = {
  /*存储用户信息全局变量*/
  account: JSON.parse(getStorage('account') || null),
  LockPool: JSON.parse(getStorage('LockPool') || null),
  LockPoolInfo: JSON.parse(getStorage('LockPoolInfo') || null),
  Balance: JSON.parse(getStorage('Balance') || 0),
  LPliquidityInfo: JSON.parse(getStorage('LPliquidityInfo') || null),
  LPliquidity: JSON.parse(getStorage('LPliquidity') || null),
  LPCoin: JSON.parse(getStorage('LPCoin') || null),
  usdtCoin: JSON.parse(getStorage('usdtCoin') || null),
  mxtCoin: JSON.parse(getStorage('mxtCoin') || null),
  DappPoolInfo: JSON.parse(getStorage('DappPoolInfo') || null),
   LPInfo: JSON.parse(getStorage('LPInfo') || null),
   mxtCoinInfo: JSON.parse(getStorage('mxtCoinInfo') || null),
   DappPool: JSON.parse(getStorage('DappPool') || null),
}

const getters = {
  /*获取用户信息*/
  getIsApprove: function (state) {
    let isApprove = null;
    if (state.isApprove) {
      isApprove = state.isApprove;
    } else {
      isApprove = JSON.parse(getStorage('isApprove') || null);
    }
    return isApprove
  },

}

const mutations = {
  set_LockPool: (state, data) => {
    state.LockPool = data;
  },
  set_LockPoolInfo: (state, data) => {
    state.LockPoolInfo = data;
  },
  set_Balance: (state, data) => {
    state.Balance = data;
  },
  set_account: (state, data) => {
    state.account = data;
  },
  set_LPliquidityInfo: (state, data) => {
    state.LPliquidityInfo = data;
  },
  set_LPliquidity: (state, data) => {
    state.LPliquidity = data;
  },
  set_LPCoin: (state, data) => {
    state.LPCoin = data;
  },
  set_usdtCoin: (state, data) => {
    state.usdtCoin = data;
  },
    set_mxtCoin: (state, data) => {
    state.mxtCoin = data;
  },
  set_mxtCoinInfo: (state, data) => {
    state.mxtCoinInfo = data;
  },
  set_LPInfo: (state, data) => {
    state.LPInfo = data;
  },
  set_DappPool: (state, data) => {
    state.DappPool = data;
  },
  set_DappPoolInfo: (state, data) => {
    state.DappPoolInfo = data;
  },
}

const actions = {
  add_LockPool: ({ commit }, data) => {
    let info = {};
    getWeb3.Init(DOGE_pool, '0xb44ef24a4109678dd850b3f37e3071d151020701', (contract, addr, balance) => {
      commit('set_LockPool', contract);
      commit('set_account', addr);
      commit('set_Balance', balance);
      info.address = '0xb44ef24a4109678dd850b3f37e3071d151020701'
      contract.methods.getMiningInfo(addr).call().then((res) => {
      
        info.info = res.ct;
        info.inviteCode = res.inviteCode;
        info.referrer = res.referrer;
        
      }).then(()=>{
        contract.methods.getTokenPrice('0x8645cdcc3aeff05672a784694190418bd79ff01e','0x55d398326f99059fF775485246999027B3197955','1000000000000000000').call().then((res2) => {
          info.MXTPrice =web3.utils.fromWei(res2, 'ether') > 0.3 ? web3.utils.fromWei(res2, 'ether') : 0.3;
          console.log(web3.utils.fromWei(res2, 'ether'),info)
          commit('set_LockPoolInfo', info);
        })
      })

    })
  },

  add_LPliquidity: ({ commit }, data) => {
    let info = {};
    getWeb3.Init(LP, '0x646b8eb9e08add2f780555f7acd0cf7582e44a28', (contract, addr, balance) => {
      commit('set_LPliquidity', contract);
      // commit('set_account', addr);
      // commit('set_Balance', balance);
      info.address = '0x646b8eb9e08add2f780555f7acd0cf7582e44a28';
      contract.methods.getUserInfo(addr,0).call().then((res) => {
      
        info.info = res.ct;
        console.log(res)
        commit('set_LPliquidityInfo', info);
      })

    })
  },
  // LP合约对象
  add_LPCoinContract: ({ commit }, data) => {
    let lp={}
    getWeb3.Init(coin_lp,"0x6d2cf46f6d13da0c913e552e5cb72081862ef9ba", (contract, addr, balance) => {
      commit('set_LPCoin', contract);
      contract.methods
      .getReserves()
      .call()
      .then((res) => {
        //LP总市值
        lp.liquidity =res[1];
        console.log('liquidity=========',res[1],lp.liquidity)
      }).then(()=>{
        contract.methods.totalSupply()
        .call()
        .then((res2) => {
           //LP供应总数
           lp.totalSupply = res2
         console.log('usdt+CHE余额==========',lp)
         commit('set_LPInfo', lp);
        })
      })
    })
  },

    // 获取usdt币合约对象
    add_usdtCoinContract: ({ commit }, data) => {
      getWeb3.Init(DOGES,"0x5bb906F094cffF3141eA31c20AC7b966026e6Fb8", (contract, addr, balance) => {
        commit('set_usdtCoin', contract)
      })
    },

       //MXT币
        add_mxtCoinContract: ({ commit }, data) => {
          var mxtInfo={};
          getWeb3.Init(DOGES, "0xecac44e162199c1d8e350d50cbefc424a142120a", (contract, addr, balance) => {
            commit('set_mxtCoin', contract);
            contract.methods
              .balanceOf("0x000000000000000000000000000000000000dead")
              .call()
              .then((res) => {
                mxtInfo.dead = res;
                // commit('set_mxtCoinInfo', mxtInfo)
               
              }).then(()=>{
                // 获取底池数量
                contract.methods
                .balanceOf(addr)
                .call()
                .then((data) => {
                  mxtInfo.balance = data;
                  console.log('MXT=========',mxtInfo)
                  commit('set_mxtCoinInfo', mxtInfo)
                });
              })
          })
        },
        
        // 销毁挖矿dapp
        add_DappPool: ({ commit }, data) => {
          let info = {};
          getWeb3.Init(depositPool, '0x70eb5099bea16076dc1c336c634bc56be22f9907', (contract, addr, balance) => {
            commit('set_DappPool', contract);
            commit('set_account', addr);
            commit('set_Balance', balance);
            info.address = '0x70eb5099bea16076dc1c336c634bc56be22f9907'
            contract.methods.getMiningInfo(addr).call().then((res) => {
        
              info.info = res.ct;
              info.inviteCode = res.inviteCode;
              info.referrer = res.referrer;
              
            }).then(()=>{
              contract.methods.getTokenPrice('0x5bb906F094cffF3141eA31c20AC7b966026e6Fb8','0xecac44e162199c1d8e350d50cbefc424a142120a','1000000000000000000').call().then((res2) => {
                info.MXTPrice =web3.utils.fromWei(res2, 'ether');
                console.log(web3.utils.fromWei(res2, 'ether'),info)
                commit('set_DappPoolInfo', info);
              })
            })
      
          })
        },
  // 锁粉
  // add_invitationContract: ({ commit }, data) => {
  //   var thisArr = [
  //     '0xb8f85f478d39affae2b0f27a3cdf310341e2c8ed',
  //     '0xa4159092d871843fdb75dd0e2ef263a9bb5d2e90',
  //     '0x9eb2425bb28300424839b9448107bbebe566bd7b',
  //   ];
  //   var thisIndex = Math.floor((Math.random() * thisArr.length));
  //   var addrH = thisArr[thisIndex]
  //   localStorage.setItem('adrr',addrH );
  //   console.log(addrH)
  //   getWeb3.Init(invitation, addrH, (contract, addr, balance) => {
  //     commit('set_invitationContract', contract);

  //   })
  // },

  // 转账
  // add_transferContract: ({ commit }, data) => {
  //   getWeb3.Init(invitation, '0x7a55a4888979f43bd983c9ab15f64ee58eca49d0', (contract, addr, balance) => {
  //     commit('set_transferContract', contract);

  //   })
  // },

  // 获取DOG币合约对象
  // add_dogCoinContract: ({ commit }, data) => {
  //   getWeb3.Init(DOGES, '0xea2f070c1b72012afb0ec30c267a792424027688', (contract, addr, balance) => {
  //     commit('set_dogCoinContract', contract);
  //     commit('set_account', addr);
  //     contract.methods
  //       .balanceOf(addr)
  //       .call()
  //       .then((res) => {
  //         console.log(web3.utils.fromWei(res, "Gwei"))
  //         commit('set_FIVEBalance', res)
  //       });

  //   })
  // },
}
export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}




