<script type="es6">
import HeadTop from "../../components/Header";
import FontCon from "../../components/Footer";
import { mapActions, mapState } from "vuex";
import Web3 from "web3";
export default {
  data() {
    return {
      lang: "",
      currency: "",
      link: "--",
      coinBalance: 0,
      isApprove: false,
      stakeAmount: null,
      withdrawAmount: "",
      timer: null,
      timer2: null,
      show: false,
      isHome: true,
      nbn: null,
      time: "1232938293",
    };
  },
  computed: {
    ...mapState("main", [
      // "priceHMD",
      // "priceCNTM",
      "hmdInfo",
      "account",
      "balance",
      "mxtCoinContract",
      "mxtContract",
      "hmdBalance",
    ]),
  },
  components: {
    HeadTop,
    FontCon,
  },
  methods: {
    ...mapActions("main", [
      // "add_price",
      "add_hmdInfo",
      "add_mxtCoinContract",
      "add_hmdCoinContract",
    ]),
    showPop: function (arg, callback) {
      if (arg == "show") {
        this.show = true;
      }
    },
    pirceInput(type) {
      if (type == 1) {
        let str = "" + this.stakeAmount;
        if (str.indexOf(".") !== -1) {
          let arr = str.split("");
          arr.splice(arr.length - 1);
          let str2 = arr.join("");
          this.stakeAmount = +str2;
        }
      } else {
        let str = "" + this.withdrawAmount;
        if (str.indexOf(".") !== -1) {
          let arr = str.split("");
          arr.splice(arr.length - 1);
          let str2 = arr.join("");
          this.withdrawAmount = +str2;
        }
      }
    },
    protectAddress: function (addr, num) {
      if (addr) {
        var newAddr = addr.replace('"', "").replace('"', "");
        if (newAddr == "0x0000000000000000000000000000000000000000") {
          return "--";
        }
        if (!newAddr) {
          return "--";
        }
        if (newAddr == 1) {
          return newAddr;
        }
        return (newAddr + "").length == 42
          ? newAddr.substring(0, num) + "****" + newAddr.substring(39, 42)
          : newAddr;
      }
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    ceilNum(d, prex = 4) {
      if (parseInt(d) == d) return d;
      let len = 1;
      let i = 0;
      while (parseInt(d) != d) {
        d = d * 10;
        len *= 10;
        i++;
      }
      let last = Math.abs(i <= prex ? 0 : d % Math.pow(10, i - prex));
      let d1 = d - last;
      if (last > 0) {
        d1 = d1 + Math.pow(10, i - prex);
      }
      d1 = d1 / len;
      return d1;
    },
    getNbn(e, type) {
      if (type == 1) {
        var val = e;
      } else {
        var val = e.target.value;
      }
      if (!e) return;

      this.mxtContract.methods
        .getTokenPrice(
          "0xEc0029a08BcD725Ff3327288bEFB99ecEE29Bc36",
          "0x6e42eae3317a19eb4b5115b0bce388bbf21cb917",
          "1000000000"
        )
        .call()
        .then((res) => {
          var five = this.ceilNum(Web3.utils.fromWei(res, "ether"));
          this.nbn = five * this.stakeAmount;
          // console.log(res,Web3.utils.fromWei(res, 'ether'),Web3.utils.toWei(val, 'ether'))
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    //取消抵押
    withdraw: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      var myzy = web3.utils.fromWei(_this.hmdInfo.info[19], "ether");
      if (myzy <= 0) {
        _this.$toast(_this.$t("message.noZy"));
        return;
      }
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .exit()
        .send({ from: account })
        .then((res) => {
          console.log("withdraw", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_hmdInfo();
          _this.getCoinBalance();
          _this.add_hmdCoinContract();
          // }

          _this.withdrawAmount = "";
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    max: function () {
      var num = this.coinBalance;
      num = num.toString();
      let index = num.indexOf(".");
      if (index !== -1) {
        num = num.substring(0, 6 + index + 1);
      } else {
        num = num.substring(0);
      }

      this.stakeAmount = num;
      this.getNbn(num, 1);
    },
    stake: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var inviteCode = _this.hmdInfo.inviteCode.toString();
      var referrer = _this.hmdInfo.referrer.toString();
      var account = _this.account;

      if (!_this.isApprove) {
        _this.$toast(_this.$t("message.noAccredit"));
        return;
      }

      if (!referrer || referrer == "--") {
        console.log(11111, referrer);
        referrer = _this.$route.query.invite
          ? _this.$route.query.invite.toString()
          : 1;
        console.log(222222, referrer);
      }
      if (!inviteCode || inviteCode == "--") {
        inviteCode = _this.account;
      }

      if (inviteCode == referrer) {
        referrer = "1";
      }

      if (Number(_this.stakeAmount) > Number(_this.coinBalance)) {
        _this.$toast(_this.$t("message.noBanner"));
        return;
      }
      if (Number(_this.balance / 10 ** 18) < 0) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      if (Number(_this.stakeAmount) <= 0) {
        _this.$toast(_this.$t("message.noNum"));
        return;
      }
      if (Number(_this.stakeAmount) < 100) {
        _this.$toast(_this.$t("message.f_67"));
        return;
      }
      if (!referrer || referrer == "--") {
        _this.$toast(_this.$t("message.onReferees"));
        return;
      }
      console.log(
        inviteCode,
        referrer,
        _this.stakeAmount * 10 ** 18,
        web3.utils.toWei(_this.stakeAmount, "ether")
      );
      var monye = (_this.stakeAmount * 10 ** 18).toString();
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });

      this.mxtContract.methods
        .investIn(
          inviteCode,
          referrer,
          web3.utils.toWei(_this.stakeAmount, "ether")
        )
        .send({
          from: account,
        })
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_hmdInfo();
          _this.getCoinBalance();
          _this.add_hmdCoinContract();
          //   setTimeout(function(){
          //      _this.add_hmdInfo();
          //     _this.getCoinBalance();
          //     _this.add_hmdCoinContract();
          //   },10000)
          // }

          _this.stakeAmount = 1;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);

      // this.$router.push({
      //   name: this.$route.name,
      //   params: { lang: this.lang },
      //   query: { r: localStorage.getItem("r") },
      // });
    },
    // 授权
    lpApprove: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.hmdInfo.address;
      var account = this.account;
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getUrl: function () {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + hash;
      var newAddr = "";
      if (this.hmdInfo) {
        if (this.hmdInfo.referrer) {
          var addr = this.hmdInfo.inviteCode;
          newAddr = addr.replace('"', "").replace('"', "");
          this.link = url + "?r=" + newAddr;
        } else {
          this.link = url;
        }
      }
      return this.link;
    },
    // 是否授权
    isApproveFun: function () {
      var account = this.account;
      var address = this.hmdInfo.address;

      this.mxtCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
    //获取当前币种余额
    getCoinBalance: function () {
      if (this.mxtCoinContract) {
        var account = this.account;
        this.mxtCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            this.coinBalance = num;
          });
      }
    },
    get_param: function (name) {
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      var r = window.location.search.substr(1).match(reg);
      if (r != null) {
        return unescape(r[2]);
      }
      // console.log(r)
      return null;
    },
  },
  created() {
    this.add_mxtCoinContract();
    this.getCoinBalance();
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    this.currency = "HMD";
    if (this.$route.query.r) {
      localStorage.setItem("r", this.$route.query.r);
    }

    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
      this.add_hmdCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtCoinContract || !this.hmdBalance) {
          getData();
        } else {
          this.getCoinBalance();
          this.isApproveFun();
          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.btnbox {
  text-align: center;
  button {
    background: transparent;
    border: 1px solid #fff;
    color: #fff;
    border-radius: 40px;
    padding: 14px 0;
    min-width: 40%;
    text-align: center;
    margin: 0 10px;
  }
  .in {
    background: #3772ff;
    border: none;
  }
}
.top {
  margin: 90px 4% 0;
  h1 {
    font-size: 30px;
    margin-bottom: 10px;
  }
  p {
    letter-spacing: 1px;
  }
}
.info {
  margin: 45px 4% 20px;
  padding: 12px 20px;
  background: url(../../assets/img/labe/info_1.png) top center no-repeat;
  background-size: 100%;
  position: relative;
  h2 {
    font-size: 18px;
    font-family: PingFang SC-Bold, PingFang SC;
    padding: 5px 0;
  }
  .all {
    margin: 10px 0;
  }
}
.info::before {
  content: "";
  display: inline-block;
  width: 120px;
  height: 103px;
  background: url(../../assets/img/labe/info_2.png) top center no-repeat;
  background-size: 100%;
  position: absolute;
  right: 10%;
  top: -10%;
}
.zy {
  margin: 45px 4% 20px;
  background: #34383f;
  border-radius: 10px;
  padding: 30px 15px;
  i{display: block;
  width:35px ;height: 35px; background: url(../../assets/img/labe/an.png) top center no-repeat;
  background-size: 100%;
  position: absolute;right: 0;}
  span {
    color: #fcfcfd;
    font-family: PingFang SC-Regular, PingFang SC;
  }
  h3 {
    color: #006ee5;
    font-size: 20px;
    margin: 5px 0;
    font-family: PingFang SC-Bold, PingFang SC;
  }
  .MyInfo {
    margin: 10px 0;
  }
  .letTop {
    display: flex;
    position: relative;
    .tit {
      margin-left: 10px;
    }
    .name {
      font-size: 20px;
      font-family: Poppins-, Poppins;
      color: #006ee5;
      margin-bottom: 2px;
    }
  }
  .time {
    text-align: center;
    background: #141416;
    width: 70%;
    margin: 15px auto;
    padding: 15px;
    border-radius: 5px;
    .van-count-down {
      font-size: 22px;
      text-align: center;
    }
  }
  .show_one{
    margin-top: 20px;
    input{
      width: 100%;
      background: #fff;
      border-radius: 10px;
      font-size: 24px;
      padding: 8px 15px;
      box-sizing: border-box;
      border:none;
      margin: 10px 0 30px;
    }
    ul{
      margin: 30px 0;
      li{
        font-size: 15px;
        padding: 7px 0;
        color: #FCFCFD;
        span{float: right;color: #006ee5;}
      }
    }
  }
}
</style>



