<script type="es6">
import { mapActions, mapState } from "vuex";
import FontCon from "../../components/Footer";
import HeadTop from "../../components/Header";
import Web3 from "web3";
export default {
  data() {
    return {
      lang: "",
      time: 43984932483,
      money: "",
      input_cols: false,
      timer: null,
      timer2: null,
      tab: 1,
      tabsNum:1,
      coinBalance: 0,
      stakeAmount:0,
      nbn:0,
      isApprove:false,
      isApproveMXT:false,
      show:false,
      planNum:1,
       link: "--",
    };
  },
  components: {
    HeadTop,
    FontCon,
  },
  computed: {
    ...mapState("main", [
      "DappPool",
      "Balance",
      "mxtCoinInfo",
      "account",
      "DappPoolInfo",
      'usdtCoin',
      "mxtCoin"
    ]),
  },
  methods: {
    ...mapActions("main", [
      "add_usdtCoinContract",
      "add_mxtCoinContract",
      "add_DappPool",
    ]),
     openCionList() {
      this.show = !this.show;
    },
      // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
        protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      localStorage.setItem("lang", lang);
      // this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    changeTab(num) {
      this.tab = num;
    },
    goTo() {
      //直接跳转
      this.$router.push('/');
    },
    getMiningInfoFun(num){
      this.tabsNum = num
    },
     getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + hash;
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link ;
    },
        getNbn(e,type){
      if(type==1){
        var val = e;
      }else{
         var val = (e.target.value);
      }
      if(!e) return;
      // if(this.cionType == 'five'){
        var Price2= this.DappPoolInfo.MXTPrice;
      // }else{
      //   var Price2= this.DappPoolInfo.GrccPrice;
      // }
          var prex =4;
          var price = Number(val)*Number(Price2);
        
          // info.fivePrice =null;
          if (parseInt(price) == price){
             this.nbn = price;
            return 
          } 
          let len = 1;
          let i = 0;
          while (parseInt(price) != price) {
            price = price * 10;
              len *= 10;
              i++;
          }
          let last = Math.abs(i <= prex ? 0 : price % (Math.pow(10, (i - prex))));
          let d1 = price - last;
          if (last > 0) {
              d1 = d1 + Math.pow(10, (i - prex));
          }
          d1 = d1 / len;
           this.nbn = d1;
    },
    inputEnter() {
      var _this = this;
      if (_this.DappPoolInfo == null) {
        _this.$toast(_this.$t("message.noMask"));
        return;
      }
      var c = _this.money.toString();
      var a = c.split('.')[1];
      var b = (web3.utils.fromWei(_this.DappPoolInfo.info[7], 'ether')).split('.')[1];
      if (a && a.length > 1) {
        // this.input_cols=true;
        // if(!/^\d+(\.\d{1,6})?$/.test(_this.money))
        var len = c.split('.')[0].length + 1
        _this.money = Number(c.substring(0, len + 1));
        console.log(len)
        _this.$toast(this.$t("message.f5_20"));
        return
      }

    },
    getTimeFun(time) {
      if (time > 0) {
        var nowtime = new Date();
        var lefttime = time * 1000 - nowtime.getTime();//距离结束时间的毫秒数
        return lefttime;
      } else {
        return 0;
      }

    },
     changePlan(num) {
      this.planNum = num;
      this.show = !this.show;
    },
     destruction2: function () {
      if (this.DappPool == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
     this.DappPool.methods
        .upDestroyLevel(_this.planNum )
        .send({
          from: account,
					})
        .then((res) => {
           _this.$toast.success(_this.$t("message.dealSuccee"));
           this.add_DappPool();
          this.show = false;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    destruction: function () {
      if (this.DappPool == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.DappPool.methods
        .destroyMxt()
        .send({ from: account })
        .then((res) => {
          console.log("destroyMxt", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
             _this.getCoinBalance();
                 this.add_DappPool();
        this.add_mxtCoinContract();
        this.add_usdtCoinContract();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
        getReward: function () {
      if (this.DappPool == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.DappPool.methods
        .getReward()
        .send({ from: account })
        .then((res) => {
          console.log("getReward", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
             _this.getCoinBalance();
                 this.add_DappPool();
        this.add_mxtCoinContract();
        this.add_usdtCoinContract();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
      getBonus: function () {
      if (this.DappPool == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.DappPool.methods
        .getBonus()
        .send({ from: account })
        .then((res) => {
          console.log("getBonus", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
             _this.getCoinBalance();
                 this.add_DappPool();
        this.add_mxtCoinContract();
        this.add_usdtCoinContract();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    investIn: function () {
      if (this.DappPool == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var inviteCode = _this.DappPoolInfo.inviteCode.toString();
      var referrer = _this.DappPoolInfo.referrer.toString();
      var account = _this.account;

      if (!_this.isApprove) {
        _this.$toast(_this.$t("message.noAccredit"));
        return;
      }

      if (!referrer || referrer == "--") {
        referrer = _this.$route.query.invite ? _this.$route.query.invite.toString() : 1;
      }
      if (!inviteCode || inviteCode == "--") {
        inviteCode = _this.account;
      }

      if (inviteCode == referrer) {
        referrer = "1";
      }
if ((Number(_this.stakeAmount)  % 100) > 0 ) {
        _this.$toast(_this.$t("message.yu"));
        return;
      }
      if (Number(_this.stakeAmount) > Number(_this.coinBalance)) {
        _this.$toast(_this.$t("message.noBanner"));
        return;
      }
      

      if (Number(_this.balance / 10 ** 18) < 0) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      if (Number(_this.stakeAmount) <= 0) {
        _this.$toast(_this.$t("message.noNum"));
        return;
      }
       if (Number(_this.stakeAmount) < 100) {
        _this.$toast(_this.$t("message.f_67"));
        return;
      }
      if (!referrer || referrer == "--") {
        _this.$toast(_this.$t("message.onReferees"));
        return;
      }
      var monye = (_this.stakeAmount * 10 ** 18).toString();
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      // var tokenArr = _this.cionType =='five'?_this.ArrList.FiveArr:_this.ArrList.GtArr;
      // console.log('investIn=======',inviteCode, referrer , web3.utils.toWei(_this.stakeAmount.toString(), 'ether'),tokenArr)
      console.log(inviteCode, referrer , web3.utils.toWei(_this.stakeAmount.toString(), 'ether'),'0xecac44e162199c1d8e350d50cbefc424a142120a')
      this.DappPool.methods
        .investIn(inviteCode, referrer , web3.utils.toWei(_this.stakeAmount.toString(), 'ether'),'0xecac44e162199c1d8e350d50cbefc424a142120a')
        .send({
          from: account,
					})
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.getCoinBalance();
                 this.add_DappPool();
        this.add_mxtCoinContract();
        this.add_usdtCoinContract();

          _this.stakeAmount =0;
          _this.nbn=0;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
                   // mxt授权
  mxtApprove: function () {
      if (this.mxtCoin == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.DappPoolInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtCoin.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApproveMXT = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
               // Five授权
    Approve: function () {
      if (this.usdtCoin == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.DappPoolInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.usdtCoin.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
      // 是否授权
    isApproveFun: function () {
      var account = this.account;
      var address = this.DappPoolInfo.address;

      this.usdtCoin.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log('usdt=========isApproveFun',res,account, address)
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
         // MXTC是否授权
    MXTisApproveFun: function () {
      var account = this.account;
      var address = this.DappPoolInfo.address;

      this.mxtCoin.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log('=========GCoinContract',res,account, address)
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApproveMXT = true;
          }
        });
    },
       //获取当前币种余额
    getCoinBalance: function () {
      if (this.usdtCoin) {
        var account = this.account;
        this.usdtCoin.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            // var num = web3.utils.fromWei(res, "ether");
            // num = num.toString();
            // let index = num.indexOf(".");
            // if (index !== -1) {
            //   num = num.substring(0, 6 + index + 1);
            // } else {
            //   num = num.substring(0);
            // }
            // console.log(num);
            this.coinBalance = res;
          });
      }
    },
  },
  mounted() {
    var lang = localStorage.getItem("lang");
    this.changeLanguage(lang || "en");
    var _this = this;

    var getData = () => {
          this.add_DappPool();
      this.add_mxtCoinContract();
      this.add_usdtCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.DappPool || !this.Balance || !this.DappPoolInfo || !this.mxtCoinInfo || !this.mxtCoin) {
          getData();
        } else {
          this.getCoinBalance();
          this.isApproveFun();
          this.MXTisApproveFun();
           if (_this.DappPoolInfo.inviteCode || _this.DappPoolInfo.referrer) {
            var invite = _this.DappPoolInfo.inviteCode ;
            _this.getUrl(invite);
          }
          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

