import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/page/home/home'
import Defi from '@/page/home/defi'
import Lock from '@/page/home/lock'
import Liquid from '@/page/home/liquid'
import Destruction from '@/page/home/destruction'
Vue.use(Router);


//获取原型对象上的push函数
const originalPush = Router.prototype.push
    //修改原型对象中的push方法
Router.prototype.push = function push(location) {  
    return originalPush.call(this, location).catch(err => err)
}

export default new Router({
    routes: [
        {
            path: '/home',
            name: 'home',
            component: Home
        },
        {
            path: '/defi',
            name: 'defi',
            component: Defi
        },
        {
            path: '/',
            name: 'lock',
            component: Lock
        },
        {
            path: '/liquid',
            name: 'liquid',
            component: Liquid
        },
        {
            path: '/destruction',
            name: 'destruction',
            component: Destruction
        }
    ]
})


